import { addSeconds, format, formatRelative, isBefore } from "date-fns";
export const RESOLUTION_STATES = {
    PRE_DRAFT: "pre-draft",
    // transition to when approved
    NOTICE: "notice",
    // transition to when notice period ends
    VOTING: "voting",
    // transition to when voting period ends
    ENDED: "ended",
};
// TODO, once we will have the ACL, we will do the logic here in the function to disable or not the action button
export const RESOLUTION_ACTIONS = {
    [RESOLUTION_STATES.PRE_DRAFT]: () => ({ label: "Edit", disabled: false }),
    [RESOLUTION_STATES.NOTICE]: () => ({ label: "View", disabled: false }),
    [RESOLUTION_STATES.VOTING]: () => ({
        label: "View and vote",
        disabled: false,
    }),
    [RESOLUTION_STATES.ENDED]: () => ({ label: "View", disabled: false }),
};
export const getDateFromUnixTimestamp = (unixTs) => new Date(Number(unixTs) * 1000);
export const getRelativeDateFromUnixTimestamp = (unixTs, baseDate = new Date()) => formatRelative(getDateFromUnixTimestamp(unixTs), baseDate);
export const getResolutionTypeInfo = (resolution) => {
    if (resolution.approveTimestamp === "0") {
        return {
            noticePeriodEnds: null,
            noticePeriodEndsAt: null,
            votingEnds: null,
            votingEndsAt: null,
        };
    }
    const noticePeriodEnds = addSeconds(getDateFromUnixTimestamp(resolution.approveTimestamp), Number(resolution.resolutionType.noticePeriod));
    const votingEnds = addSeconds(noticePeriodEnds, Number(resolution.resolutionType.votingPeriod));
    return {
        noticePeriodEnds,
        noticePeriodEndsAt: format(noticePeriodEnds, "dd LLL yyyy, H:mm:ss"),
        votingEnds,
        votingEndsAt: format(votingEnds, "dd LLL yyyy, H:mm:ss"),
    };
};
export const getResolutionState = (resolution, $currentTimestamp, resolutionTypeInfo) => {
    if (resolution.approveTimestamp !== "0") {
        const { noticePeriodEnds, votingEnds } = resolutionTypeInfo;
        if (isBefore(new Date($currentTimestamp), noticePeriodEnds)) {
            return RESOLUTION_STATES.NOTICE;
        }
        if (isBefore(new Date($currentTimestamp), votingEnds)) {
            return RESOLUTION_STATES.VOTING;
        }
        return RESOLUTION_STATES.ENDED;
    }
    return RESOLUTION_STATES.PRE_DRAFT;
};
export const getEnhancedResolutionMapper = ($currentTimestamp) => (resolution) => {
    const resolutionTypeInfo = getResolutionTypeInfo(resolution);
    const state = getResolutionState(resolution, $currentTimestamp, resolutionTypeInfo);
    return Object.assign(Object.assign({}, resolution), { state, createdAt: getRelativeDateFromUnixTimestamp(resolution.createTimestamp), updatedAt: resolution.updateTimestamp !== "0"
            ? getRelativeDateFromUnixTimestamp(resolution.updateTimestamp)
            : null, approvedAt: resolution.approveTimestamp !== "0"
            ? getRelativeDateFromUnixTimestamp(resolution.approveTimestamp)
            : null, href: state === RESOLUTION_STATES.PRE_DRAFT
            ? `#/resolutions/${resolution.id}/edit`
            : `#/resolutions/${resolution.id}`, action: RESOLUTION_ACTIONS[state](), resolutionTypeInfo });
};
export const getEnhancedResolutions = (resolutions, $currentTimestamp) => {
    const mapper = getEnhancedResolutionMapper($currentTimestamp);
    return resolutions.map(mapper);
};
